* {
    margin: 0;
    padding: 0;
}

.Home {
    height: 100%;
    width: 100%;
}

.Navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.4cm 1cm;
    background-color: blueviolet;
}

.logo{
    width:4cm;
    height:1cm;
}
.logo-container {

}

.menu-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    column-gap: 0.5cm;
}

.Body {
    background-color: aquamarine;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.intro {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1cm;
    column-gap: 0.5cm;
    margin:0.5cm;
}

.para {
    font-size: 14px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 0.5px;
    display: flex;
    flex-direction: column;
    row-gap: 0.2cm;
}

.para1 {
    font-size: 50px;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width:fit-content;
}

.para1:hover {
    background: linear-gradient(to right, #a60643,
            #d05808, #e70fe7, #f6e765, rgb(162, 1, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.para2 {
    font-size: 16px;
    width: 70%;
}

.photo {
    padding: 1cm;
}

.p-photo {
    height: 10cm;
    width: 10cm;
    border-radius: 50%;
}

.footer {
    background-color: aqua;
}

.aboutMe{
    margin:0.5cm;
}

.projectsMain{
    margin:0.5cm;
}
.left{
    display: flex;
    flex-direction: row;
    justify-content: left;
}